.arrow {
  width: 80px;
  height: 80px;
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
  position: absolute;
  top: 50%;
  margin-top: -40px;
}

.arrow.left {
  left: 0px;
  margin-left: -100px;
}

.arrow.right {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  right: 0px;
  margin-right: -100px;
}

.arrow-icon polyline {
  -webkit-transition: all 100ms ease-in-out;
  -o-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
}

.arrow:hover .arrow-icon polyline {
  stroke-width: 3;
}

.arrow:active .arrow-icon polyline {
  stroke-width: 6;
}



