.about {
  height: calc(100vh - 80px);
  max-height: calc(100vh - 80px);
  overflow-y: scroll;
  margin: 0 auto;
  padding: 20px;
  width: 72%;
}

.about__header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin-bottom: 30px;
}

.personal-picture {
  height: 200px;
  border-radius: 5%;
  float: left;
}

.name-box {
  border-left: 1px solid rgb(100, 100, 100);
  margin-left: 20px;
  padding-left: 20px;
}

.name {
  font-size: 64px;
}

.job-title {
  font-size: 18px;
  padding-left: 4px;
}

.title {
  line-height: 2em;
  margin-bottom: 10px;
  border-bottom: 1px solid rgb(100, 100, 100);
}

.paragraph {
  line-height: 1.5em;
  margin-bottom: 10px;
}


@media (max-width: 800px) {
  .about {
    width: 100%;
    padding: 0 10px;
  }
}

@media (max-width: 650px) {
  .about__header {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .about__name-box {
    border-left: none;
    border-top: 1px solid rgb(100, 100, 100);
    margin: 20px 0 0 0;
    padding: 10px 0 0 0;
    text-align: center;
  }

  .job-title {
    padding: 0;
  }
}


@media (max-width: 500px) {
  .name {
    font-size: 36px;
  }

  .job-title {
    font-size: 18px;
  }
}



