.projects {
  width: 100%;
  height: calc(100vh - 80px);
  padding: 0px 30px;
  margin: 0 auto;
  background: transparent;
  position: relative;
}

.carousel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.carousel__selection {
  margin: 5px;
  text-align: center;
}

@media (max-width: 650px) {
  .projects {
    padding: 0px 10px;
  }

  .arrow.right,
  .arrow.left {
    margin: 0;
  }
}



